import axios from 'axios'

const catchNetWork = function(axiosPromise){
  return new Promise((resolve, reject) => {
    axiosPromise.then(response => resolve(response))
      .catch(error => {
        reject(error);
      });
  });
};

export default class Fetch{
  constructor(path){
    this.path = path;
    this.axios = axios;
  }

  // get请求
  getServerData(param){
    console.log(`%c Fetch.js url：${this.path}?${param}`, "color:#31C27C");
    return catchNetWork(
      this.axios.get(`${this.path}?${param}`)
    )
  }
}



